.ag-theme-alpine {
    height: 800px;
    width: 1030px;
    background-color: lightgray;
    margin-left: 50px;
    margin-top: 20px;
    position: absolute;
    left:50vh;
    top:1700px;
  
  }
  
  .custom-header {
    background-color: #e6e6e6e3;
    color: rgb(0, 0, 0);
    width:20px;
    
  }
  
  .custom-header1 {
    background-color: #53ca57 ;
    color: rgb(0, 0, 0);
    width:20px;
    
  }
  
  .custom-header2 {
    background-color: #ffe252;
    color: rgb(0, 0, 0);
    width:20px;
    
  }
  
  .custom-header3 {
    background-color: #fa890f;
    color: rgb(0, 0, 0);
    width:20px;
    
  }
  
  .custom-header4 {
    background-color: #eb3333;
    color: rgb(0, 0, 0);
    width:20px;
    
  }
  
  .custom-header5 {
    background-color: #900101e3;
    color: rgb(255, 255, 255);
    width:20px;
    
  }
  
  .ag-theme-alpine .ag-header-cell {
    width: 20px; /* You can adjust this width */
  }
  
  .ag-theme-alpine .ag-cell {
    padding: 8px; 
    width: 20px;
  }
  
  .ag-theme-alpine .ag-row.ag-row-level-0 {
      background-color: #fffefe;
      min-width: 80px;
    }
    
    .ag-theme-alpine .ag-row.ag-row-level-1 {
      background-color: #e6e5e5b3;
    }
  
  
  
  button {
    background-color: transparent;
    color: var(--gh-blue-5);
    gap: 8px;
    border: none;
    cursor: pointer;
  }
  
  .button-td {
    padding: 0 10px;
    width: 2px;
  }